export default {
	data() {
		return {
			// 左侧图片分类列表
			pics: [],
			// 默认选中分类的索引
			index: 0,
			// 获取右侧数据
			cates: []
		};
	},
	onLoad() {
		// 获取左侧图片分类
		this.getPics();
	},
	methods: {
		// 获取左侧图片分类
		async getPics() {
			const res = await uni.get('/api/getimgcategory');
			console.log(res);
			if (res[1].data.status !== 0) {
				return uni.baseToast('图片分类请求失败');
			}
			this.pics = res[1].data.message;
			console.log(this.pics);
			this.getCates(4, this.pics[4].id);
		},
		// 点击左侧分类
		async getCates(i, id) {
			this.index = i;
			// 发起请求获取右侧分类
			const res = await uni.get('/api/getimages/' + id);
			console.log(res);
			if (res[1].data.status !== 0) {
				return uni.baseToast('图片分类请求失败');
			}
			this.cates = res[1].data.message;
			console.log(this.cates);
		},
		// 点击预览图片
		look(i){
			var urls = this.cates.map((item)=>{
				return item.img_url
			})
			console.log(urls)
			uni.previewImage({
				urls,
				current: i
			})
		}
	}
};
